<script setup lang="tsx">
import {AppGrid} from '@/components/grid';
import {OrderListService} from '@/services/order-list.service';
import {buildOrderConfig} from '@/logic/order-config';
import {pickGridColumns} from '@/logic/common';
import {SupabaseService} from '@/services/supabase.service';

const config = ref(null as any);

onMounted(async () => {
  const items = await useService(SupabaseService).getMany(async x =>
    x.from('orderStatusType').select(),
  );

  const orderStatusOptions = items.map(({id, name, severity}) => ({
    value: id,
    label: name,
    severity,
  }));

  config.value = pickGridColumns(
    buildOrderConfig(orderStatusOptions, 'user-list'),
    [
      'id',
      'shippingMethodName',
      'vatDocumentRequired',
      'productsPriceToPay',
      'shippingPriceToPay',
      'totalPriceToPay',
      'orderStatusTypeId',
      'comment',
    ],
    {
      title: 'Мои заказы',
      getData: () => useService(OrderListService).getUserOrders() as any,
    },
  );
});
</script>

<template>
  <AppGrid v-if="config" :config="config" />
</template>
